import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export type UsersState = {
  ids: number[];
  loading: boolean;
};

const initialState: UsersState = {
  ids: [],
  loading: false,
};

export const usersSlice = createSlice({
  name: "users",
  initialState,
  reducers: {
    fetchUsersFailure: (
      state: UsersState,
      action: PayloadAction
    ): UsersState => ({
      ...state,
      loading: false,
    }),
    fetchUsersRequest: (
      state: UsersState,
      action: PayloadAction
    ): UsersState => ({
      ...state,
      loading: true,
    }),
    fetchUsersSuccess: (
      state: UsersState,
      action: PayloadAction<number[]>
    ): UsersState => ({
      ...state,
      loading: false,
      ids: action.payload,
    }),
  },
});

export const { fetchUsersFailure, fetchUsersRequest, fetchUsersSuccess } =
  usersSlice.actions;
